:root{--gap: 15px;--radius: 4px}*,*::after,*::before{margin:0;padding:0;box-sizing:border-box;user-select:none;scrollbar-color:rgba(48,130,255,0.2);scrollbar-width:thin}::-webkit-scrollbar{width:4px;height:4px}::-webkit-scrollbar-track{background-color:rgba(48,130,255,0.2)}::-webkit-scrollbar-thumb{background-color:rgba(48,130,255,0.3)}#root,body{width:100%;font-size:16px;font-family:'Nunito', sans-serif;text-rendering:optimizeLegibility;text-size-adjust:100%;-webkit-font-smoothing:antialiased;color:#2e3032}body{background-color:#F9F9F9;background-image:radial-gradient(#3082ff 0.5px, transparent 0.5px),radial-gradient(#3082ff 0.5px, #ececec 0.5px);background-size:20px 20px;background-position:0 0,10px 10px}#root{display:flex;justify-content:center;align-items:center;min-height:100vh}li{list-style:none}input,button,textarea{font-family:'Nunito', sans-serif;border:none}.main{position:relative;width:540px;height:600px;display:grid;grid-template-areas:"header" "container";grid-template-columns:1fr;grid-template-rows:auto 1fr;grid-gap:var(--gap);padding:var(--gap);overflow:hidden;border-radius:var(--radius);background-color:#F9F9F9}@media (max-width: 600px){.main{width:100%;height:100vh}}.auth{position:absolute;width:100%;height:100%;display:flex;justify-content:center;align-items:center;background-color:rgba(249,249,249,0.35);backdrop-filter:blur(4px);z-index:20}.header{grid-area:header}.container{grid-area:container;display:grid;grid-template-areas:"chat" "form";grid-template-columns:1fr;grid-template-rows:1fr auto;grid-gap:var(--gap);position:relative;overflow:hidden}.chat{grid-area:chat;overflow-y:hidden}.form{grid-area:form;display:grid;grid-gap:var(--gap)}.notification{display:block;padding:4px var(--gap);background-color:#dd2344;color:#F9F9F9}.header{display:flex;flex-direction:column}.header__row{display:flex;justify-content:space-between}.header__title{font-size:1rem;font-weight:500;text-transform:capitalize}.header__title::before{content:'';display:inline-block;width:8px;height:8px;margin-right:4px;background-color:#45cb59;border-radius:50%}.header__button{background-color:transparent}.header__icon{display:flex;justify-content:center;align-items:center;width:25px;height:25px;text-transform:uppercase;font-size:.75rem;font-weight:500;line-height:1;cursor:pointer}.header__icon path,.header__icon polygon,.header__icon rect{fill:#3082ff}.chat__list{display:grid;align-content:start;grid-gap:calc( var(--gap) * .75);height:100%;padding:var(--gap);overflow-y:auto;overflow-x:hidden;transition:2000ms linear}.chat__item{position:relative;display:grid;justify-self:end;width:auto;max-width:70%;height:auto;border-radius:calc( var(--radius) * 2);border-bottom-right-radius:0;background-color:rgba(48,130,255,0.8);color:#F9F9F9;animation:msg linear 250ms}.chat__item:hover{background-color:rgba(48,130,255,0.85)}.chat__item--current{justify-self:start;border-radius:calc( var(--radius) * 2);border-bottom-left-radius:0;background-color:#e5e5e5;color:#2e3032;animation:msg-r linear 250ms}.chat__item--current .chat__username{justify-self:start;color:rgba(0,0,0,0.65)}.chat__item--current .chat__like-c{left:calc(100% - (var(--gap) * -2) - 16px)}.chat__item--current .chat__bg{border-radius:calc( var(--radius) * 3);border-bottom-left-radius:0}.chat__item--current:hover{background-color:#e5e5e5}.chat__content{display:grid;grid-gap:4px;padding:8px 14px;cursor:pointer}.chat__description{font-size:.833rem;line-height:1.35;font-weight:500}.chat__username{justify-self:end;font-weight:300;font-size:.694rem;color:rgba(249,249,249,0.75);text-transform:capitalize}.chat__like{width:16px;height:16px}.chat__like-c{display:grid;position:absolute;left:calc(var(--gap) * -2)}.chat__like:hover{cursor:pointer}.chat__like:hover path,.chat__like:hover polygon,.chat__like:hover rect{fill:#dd2344;stroke:#dd2344}.chat__counter{text-align:center;font-size:.694rem;font-weight:500;color:#bebec2}.chat__bg{position:absolute;top:0;left:0;display:flex;justify-content:center;align-items:center;height:100%;width:100%;z-index:10;border-radius:calc( var(--radius) * 3);border-bottom-right-radius:0;background-color:rgba(46,48,50,0.25);backdrop-filter:blur(3px)}.chat__delete{width:20px;height:20px;fill:#F9F9F9;stroke:#F9F9F9}.chat__delete:hover{cursor:pointer;transform:scale(1.1)}.form__input{width:100%;height:40px;padding:var(--gap);resize:none;overflow:hidden;border:2px solid #bebec2;background-color:#F9F9F9;border-radius:var(--radius)}.form__submit{width:85px;height:35px;justify-self:end;font-size:.833rem;font-weight:500;letter-spacing:.5px;appearance:none;border:none;cursor:pointer;border-radius:var(--radius);box-shadow:0 0 10px 2px rgba(48,130,255,0.25),0 0 20px 6px rgba(48,130,255,0.05);background-color:#3082ff;color:#F9F9F9}.form__submit:hover{background-color:#1773ff}.auth__form{display:grid;grid-gap:10px;place-content:center;width:100%;height:100%;padding:var(--gap)}.auth__row{display:grid;position:relative;width:280px;height:40px}.auth__title{font-size:1.44rem;letter-spacing:-.01rem}.auth__label{position:absolute;left:var(--gap);top:50%;transform:translateY(-50%);font-size:.833rem;letter-spacing:.025rem;color:rgba(46,48,50,0.75)}.auth__input,.auth__submit,.auth__create{width:100%;height:100%;padding:0 var(--gap);font-size:.833rem;border-radius:var(--radius);border:1px solid transparent}.auth__input{letter-spacing:.025rem;border:1px solid #bebec2;background-color:#F9F9F9}.auth__submit,.auth__create{cursor:pointer;text-align:center;letter-spacing:.02rem}.auth__submit{background-color:#3082ff;color:#F9F9F9;box-shadow:0 0 10px 2px rgba(48,130,255,0.25),0 0 20px 6px rgba(48,130,255,0.05)}.auth__create{background-color:#F9F9F9;border:1px solid rgba(46,48,50,0.75);box-shadow:0 0 10px 2px rgba(190,190,194,0.35),0 0 20px 6px rgba(190,190,194,0.15)}.auth__hr{width:100%;height:1px;margin:var(--gap) 0;border:none;background-color:#bebec2}@keyframes msg{0%,20%{transform:translateX(-50px);opacity:0}100%{transform:translateX(0);opacity:1}}@keyframes msg-r{0%{transform:translateX(50px);opacity:0}to{transform:translateX(0);opacity:1}}
